<script>
	import {getUserFullInfo,getGameRecordCard,getUserInstantList,getUserFavourite} from '../../common/api.js'
	export default {
		data() {
			return {
				userInfo: null,
				gameCard: null,
				uid: 19084220
			}
		},
		methods: {
			async getUserInfo() {
				const {data: res} = await getUserFullInfo(this.uid)
				this.userInfo = res.data.user_info
				console.log(res,'info')
			},
			// getGameRecordCard 请求回来的数据为固定数据
			getGameCard() {
				const {data: res} = getGameRecordCard(this.uid)
				this.gameCard = res.list
				console.log(res,'game')
			}
		},
		onLoad() {
			uni.getSystemInfo({
				success: res => {
					const rpxNum = 750 / res.windowWidth
					this.rpxNum = rpxNum
					this.windowHeight = res.windowHeight
				}
			})
			this.getUserInfo()
			this.getGameCard()
		}
	}
</script>

